export default Ember.HTMLBars.template({"id":"CSN7hsn9","block":"{\"symbols\":[],\"statements\":[[7,\"div\"],[11,\"class\",\"col-12 col-lg-6 mr-auto ml-auto mt-5\"],[9],[0,\"\\n  \"],[7,\"h2\"],[9],[7,\"span\"],[11,\"class\",\"far fa-newspaper\"],[9],[10],[0,\" About Page\"],[10],[0,\"\\n  \"],[7,\"hr\"],[11,\"class\",\"underline\"],[9],[10],[0,\"\\n  \"],[7,\"img\"],[11,\"class\",\"embedded-logo img-fluid\"],[11,\"src\",\"../assets/images/logo.png\"],[11,\"alt\",\"Bitcorn Logo\"],[9],[10],[0,\"\\n\\n  \"],[7,\"p\"],[9],[0,\"\\n    Bitcorn is an interactive learning platform whose development\\n    started between October and November 2018. The idea for the name and the\\n    content of that website, I get while taking a walk at midday in the beginning of November 2018.\\n    Before, I just played around and created a website without a real idea in my head.\\n    Now, it is a webpage designed mainly for computer algorithms, but also maths and other stuff.\\n    Interactive means in the case of Bitcorn, that you watch a video or presentation\\n    and can play afterwards with the presented algorithm on the website.\\n    But additionally, you can also download free\\n    \"],[7,\"a\"],[11,\"href\",\"https://creativecommons.org/publicdomain/zero/1.0/deed.de\"],[11,\"target\",\"_blank\"],[11,\"rel\",\"noopener\"],[9],[0,\"CC0-licence\"],[10],[0,\"\\n    graphics, models, and fonts or get creative programming ideas in the downloads section.\\n    So, with Bitcorn you get full support for your programming career. Is this not cool?\\n  \"],[10],[0,\"\\n\"],[10]],\"hasEval\":false}","meta":{"moduleName":"bitcorn/templates/about/page.hbs"}});